@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,400&family=Playfair+Display:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MuiTableCell-root {
  font-family: 'Montserrat', sans-serif !important;
}



.App {
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header-title {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  font-size: calc(16px + 5.5vmin);
  margin: auto;
  margin-top: 2%;
  padding: 15px;
  width: 80%;
  color: rgba(0, 0, 0, 0.87)
}

.ivar-logo {
  width: calc(50px + 6vmin);
  display:inline-block;
  margin-bottom: 1%;
}

.ornament {
  width: calc(50px + 20vmin);
  display:inline-block;
  opacity: 0.5;
}
  

