.rule-header h2 {
    font-family: 'Playfair Display', serif;
    font-size: calc(14px + 0.5vmin);
}

.rule-box {
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    padding: 0 25px 25px 25px;
    font-size: 16px;
}

.rule-box p {
    text-align: justify;
}

.rule-header {
    color: white;
    background-color: lightslategray;
    text-align: left;
    padding-left: 25px;
}

table {
    table-layout: fixed;
    width: 100%;
}

table, th, td {
    border: 1px solid Gainsboro;
    border-collapse: collapse;
    border-right: none;
    border-left: none;
    color: rgba(0, 0, 0, 0.87);
    font-weight: 300;
    line-height: 1.5rem;
    font-family: 'Montserrat', sans-serif !important;
    padding: 6px 24px 6px 16px;
    line-height: 1.43rem;
    font-size: 0.875rem;
}

thead, tr, th {
    font-weight: 600;
    color: rgba(0, 0, 0, 0.87);
    line-height: 1.5rem;
    padding: 14px;
}

thead {
    background-color: Gainsboro;
    background-color: #fbfbfb;
}

blockquote {
    /*border-left: 3px solid lightslategray;*/
    padding: 10px;
    background-color: white;
    background-image: url(/nynorskpaarussisk/static/media/quote01.6b3b139d.png);
    background-repeat: no-repeat;
    background-size: 20px 20px;
}

blockquote p {
    padding: 5px;
    margin-left: 25px;
}

.hash-link {
    text-decoration: none;
}

.hash-link:focus, .hash-link:hover, .hash-link:visited, .hash-link:link, .hash-link:active {
    text-decoration: none;
    color:inherit
}


/* Citation style */
span.cite{
    color: lightslategrey;
    display: block;
    text-align: right;
}

@media only screen and (max-width: 1000px) {
    table {
        word-break: break-word;
        overflow-wrap: break-word;
        -webkit-hyphens: auto;
            -ms-hyphens: auto;
                hyphens: auto;
    }
}
